.nav {
    & button {
        // 取消 button 預設樣式
        margin: 0;
        padding: 0 5px;
        border: none;
        outline: none;
        background-color: transparent;

        position: absolute;
        right: 50px;


        @media (max-width: 800px) {
            & {
                right: 15%;
            }
        }
        @media (max-width: 400px) {
            & {
                right: 10px;
            }
        }


        & div {
            display: block;
            width: 20px;
            height: 20px;
        }
        

        & span {
            display: block;
            position: relative;
            margin: 2px 0;
            padding: 0;
            float: right;

            transition: width 0.3s;

            background-color: #e9e9e9;
        }

        & #firstSpan{
            width: 20px;
            height: 2px;
        }
        & #secondSpan{
            width: 10px;
            height: 4px;
        }
        & #thirdSpan{
            width: 15px;
            height: 2px;
        }

        &:hover {
            cursor: pointer;
        }
        &:hover #firstSpan{
            width: 15px;
        }
        &:hover #secondSpan{
            width: 20px;
        }
        &:hover #thirdSpan{
            width: 10px;
        }
    }


    & #dropDownMenu {
        position: absolute;
        left: 0;
        top: 70px;
        width: 100%;

        background-color: #363334;


        & ul {
            margin: 0;
            padding: 0;

            list-style: none;
        }

        & .menuMain{
            width: 100%;

            & li {
                padding: 5px 0;


                & a {
                    display: block;
                    margin: 0 auto;
                    padding: 10px 50px;
                    width: calc(100% - 150px);
                    // height: 100%;
                    border-radius: 5px;
                    
                    color: #FFFFFF;

                    &:hover {
                        background-color: #494647;
                    }
                }
            }
        }


        & .menuSecondary {
            & li {
                padding: 20px 65px;

                
                & a {
                    
                    & #githubImage {
                        padding: 10px;
                        width: 25px;
                        height: 25px;
                    }
                }
            }
        }
    }
}