$rwdMaxWidth: 1100px;


.footer {
    margin: 0 auto;
    padding: 100px 0;

    background-color: #242122;
    color: #e9e9e9;


    & div {
        display: flex;
        align-items: center;
        justify-content: center;


        @media (max-width: $rwdMaxWidth) {
            display: flex;
            flex-direction: column-reverse;
        }


        & section {
            margin: 0 60px;

            display: flex;


            & #copyright {
                text-align: center;
                font-size: 18px;


                @media (max-width: $rwdMaxWidth) {
                    margin: 60px 0 0 0;

                    font-size: 15px;
                }
            }

            & nav {
                margin: 0 40px;

                display: flex;
                flex-direction: column;


                @media (max-width: $rwdMaxWidth) {
                    margin: 0 20px;
                }


                & h3 {
                    color: #888888;


                    @media (max-width: $rwdMaxWidth) {
                        font-size: 14px;
                    }
                }

                & a {
                    padding: 5px 0;

                    color: white;

                    font-size: 18px;
                    
                    /* 去除 <Link> 的底線 */
                    text-decoration: none;


                    @media (max-width: $rwdMaxWidth) {
                        font-size: 15px;
                    }
                }
            }
        }
    }
}