#introduction {
    padding: 70px 0;

    background-color: #242122;
    color: white;

    text-align: center;


    & h1 {
        margin: 60px 0 0 0;
    
        color: #F36BB3;
    
        font-size: 40px;
    }

    & h2 {
        margin: 30px 10px;
    
        font-size: 25px;
        font-weight: 300;
    }
}


#description {
    padding: 30px 30px;

    background-image: linear-gradient(rgba(255, 255, 255, 0.548), rgba(255, 255, 255, 0.973)), url('../../assets/logoBackground.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;


    @media (max-width: 800px) {
        & {
            background-image: none;
        }
    }


    & article {
        margin: 60px 0;


        & h3 {
            font-weight: 500;
        }
    }
}