.verticalFilledHorizontalArrangement {
    height: 100%;

    display: flex;
    align-items: center;
}


.webBlockContainer {
    margin: 0 100px;


    @media (max-width: 800px) {
        & {
            margin: 0 50px;
            width: 80%;
        }
    }
    @media (max-width: 450px) {
        & {
            margin: 0;
            width: 100%;
        }
    }
}
  
.hoverIncreaseOpacity {
    opacity: 0.8;
}
.hoverIncreaseOpacity:hover {
    opacity: 1;

    cursor: pointer;
}