.header {
    width: 100%;
  
    position: fixed;
  
    background-color: #242122;
    border-bottom: solid 1px #312f30;


    & .div {
        height: 70px;
      
        display: flex;
        align-items: center;


        & #logo {
            margin: 0 10px;
            width: 200px;
        
        
            &:hover {
                cursor: pointer;
            }
        
            & img {
                margin: 0 10px;
          
                width: 25px;
                height: 25px;
            }
        
            & span {
                color: #F36BB3;
                font-size: 20px;
                font-weight: 700;
            }
        }
        
        & a {
            // 去除 <Link> 的底線
            text-decoration: none;
        }
    }
}